.header_child2{
    margin: 28px;
}
#id{
    display: flex;
    justify-content: flex-start;   
    align-items: center; 
    font-size: 15px;
    padding: 5px;
    gap: 10px;
    color: blue ;
}
.suggestion{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.suggestion h4{
    font-weight: 600;
}
.btns button{
    padding: 5px 20px;
    border-radius: 5px;
    background-color: aliceblue;
    border: none;
    outline: none;
    margin-right: 15px;
}
#text{
    text-align: left;
    font-size: 15px;
}
#text span{
    color: rgba(24, 24, 212, 0.779);
}
.main{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    margin: 0px 28px;
    gap: 20px;
}
.master{
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 12px;
    background-color: rgba(231, 225, 225, 0.565);
    border-radius: 5px;
}
.filter{
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 8px;
    border:2px solid rgba(128, 128, 128, 0.453);
    border-radius: 5px;
    font-size: 15px;
    background-color: white;
}
.filter input{
    outline: none;
    border: none;
    border-left: 1px solid gray;
    width: 175px;
}
.material-symbols-outlined{
    color:rgb(31, 31, 136);
}

/* folders */

.root{
    font-size: 20px;
    float: left;
    margin: 20px 28px;
    color: rgb(0, 0, 0);
}
.fa-folder{
    color: rgb(0, 0, 0);
    font-size: 20px;
}
table{
    font-size: 15px;
    width: 95%;
    text-align: left;
    margin: 0px 20px;
}
th{
    border-bottom: 1px solid rgba(128, 128, 128, 0.614);
}
tr:hover{
    background-color: rgba(128, 128, 128, 0.27);
    transition: all 0.3s linear;
    cursor: pointer;
    border-radius: 10px;
}