.details{
    display: flex;
    justify-content: space-around;
    margin-top: 18px;
}
.fa-circle-info,.fa-rotate{
    font-size: 20px;
    color: black;
}
.date{
    font-size: 14px;
    text-align: left;
    margin: 20px 18px;
}
#inner-tb{
    margin: 10px;
}
#details_table table tr th{
    border: none;
}
.img img{
    width: 125px;
}
.img p{
    text-align: left;
    font-size: 13px;
    margin: 20px 12px;
    font-weight: 600;
}
.value{
    color: rgba(24, 24, 212, 0.779);
}