.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 2px 2px 5px rgba(192, 188, 188, 0.612);
}
.navbar{
    width: 100%;
}
.nav-item > .nav-link,.navbar-brand{
    color: black;
    margin-left: 0.5rem;
}
#create{
    background-color: rgb(40, 40, 212);
    color: white;
    border-radius: 5px;
    padding: 5px 10px;
    margin-top: 2px;
}
/* icons */
.apps{
    position: relative;
    left: 0px;
    font-size: 27px;
}
.fa-bitbucket{
    color:var(--blue);
    margin: 10px 10px 10px 0;
}
.search{
    position: relative;
}
.fa-magnifying-glass{
    position: absolute;
    left: -12px;
    top: 10px;
    color: gray;
    font-size: 15px;
}
.search input{
    padding: 3px 30px;
    width: 210px;
    border:2px solid rgba(128, 128, 128, 0.453);
    border-radius: 5px;
    color: gray;
    position: relative;
    left: -20px;
    font-size: 15px;
}
.right{
    font-size: 20px;
    padding-right: 15px;
}
.fa-user{
    background-color:var(--blue);
    color: white;
    font-size: 12px;
    padding: 10px;
    clip-path: circle(45%);
    margin-right: 20px;
}
/* Hover */

.fa-solid:hover{
    color: rgba(20, 20, 212, 0.766);
}
.fa-user:hover{
    color: white;
    background-color: black;
}
.apps:hover{
    cursor: pointer;
}
.nav-item > .nav-link:hover,.navbar-brand:hover{
    color: var(--blue);
}