.child1_head{
    text-align: left;
    height: 89vh;
    background-color: aliceblue;
}

.child1_head ul{
    list-style-type: none;
    display: grid;
    justify-content: flex-start;
}
#booking_station{
    display: flex;
    gap: 12px;
    padding: 29px 0px;
    align-items: center;
}
#list{
    display: flex;
    gap: 13px;
    align-items: center;
    padding: 10px;
}
.fa-code{
    background-color:var(--blue);
    color: white;
    padding: 15px;
    border-radius: 5px;
}
.active{
    background-color: rgba(128, 128, 128, 0.057);
    border-radius: 5px;
    color: var(--blue);
}
#list:hover{
    background-color: rgba(128, 128, 128, 0.097);
    color: var(--blue);
    transition: all 0.3s linear;
    cursor: pointer;
    border-radius: 5px;
}
